<template>
  <div class="user">
    <el-dialog :title="add == 'add' ? '新增用户':'修改用户'" :visible.sync='dialogVisible'>
      <form-common :form="operateForm" :formLabel="operateFormLabel" :inline="true" ref="form" :rules="rules"></form-common>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="confirm">提交</el-button>
      </div>
    </el-dialog>
    <div class="userHeader">
      <el-button @click="setDialogVisible" type="primary" size="small">+ 新增</el-button>
      <form-common :form="searchForm" :formLabel="formLabel" :inline="true">
        <el-button type="primary" @click="getList" size="small">搜索</el-button>
      </form-common>
    </div>
    <table-common :tableLabel="tableLabel" :config="config" :tableData="tableData" @edit="userEdit"></table-common>
  </div>
</template>
<script>
import formCommon from '@/components/formCommon.vue'
import { getUserPage, sendAddressInfo, getUserInfo, sendAllUser } from '@/require/api'
import TableCommon from '@/components/tableCommon.vue'
export default {
  components: { formCommon, TableCommon },
  name: 'user',
  async mounted() {
    let { data: userPage } = await getUserPage()
    this.operateFormLabel = userPage
    let { data: userInfo } = await getUserInfo()
    this.tableData = userInfo
  },
  data() {
    return {
      add: '',
      dialogVisible: false,
      operateFormLabel: [],
      operateForm: {
        name: '',
        age: '',
        sex: '',
        birth: '',
        addr: '',
      },
      searchForm: {
        searchContent: '',
      },
      formLabel: [
        {
          model: 'searchContent',
          label: '',
          type: 'input',
        },
      ],
      rules: {
        name: [{ required: true, message: '姓名不能为空', trigger: 'blur' }],
        age: [{ required: true, message: '年龄不能为空', trigger: 'blur' }],
      },
      tableLabel: [
        {
          prop: 'name',
          label: '姓名',
        },
        {
          prop: 'age',
          label: '年龄',
        },
        {
          prop: 'sex',
          label: '性别',
        },
        {
          prop: 'birth',
          label: '出生日期',
          width: 250,
        },
        {
          prop: 'addr',
          label: '地址',
          width: 320,
        },
      ],
      tableData: [],
      config: {
        page: 1,
        total: 30,
      },
      rowData: {},
    }
  },
  methods: {
    setDialogVisible() {
      this.dialogVisible = true
      this.add = 'add'
      this.operateForm = {
        name: '',
        age: '',
        sex: '',
        birth: '',
        addr: '',
      }
    },
    async confirm() {
      if (this.add === 'add') {
        await sendAddressInfo(this.operateForm)
        let { data: userInfomation } = await getUserInfo()
        this.tableData = userInfomation
      } // else {
      //   this.tableData.find((item) => {
      //     if (item.name === this.rowData.name && item.age === this.rowData.age && item.sex === this.rowData.sex && item.birth === this.rowData.birth && item.addr === this.rowData.addr) {
      //       item = this.operateForm
      //     }
      //   })
      //   // await sendAllUser(this.tableData)
      // }

      this.dialogVisible = false
    },
    async getList() {
      await sendAddressInfo(this.operateForm)
    },
    userEdit(row) {
      // console.log(row)
      this.dialogVisible = true
      this.add = 'edit'
      this.operateForm = row
      // this.rowData = row
    },
  },
}
</script>

<style lang='less' scoped>
.userHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>